import { _decorator, CCInteger, CCString, Component, Node, NodeEventType, Sprite } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('mycard')
export class mycard extends Component {

    @property(CCString)
    indexName = '';

    start() {

    }

    initMj(num, spriteFrame, callback) {
        var x = this.getRandomInt(-300, 300);
        var y = this.getRandomInt(-200, 400);
        this.node.name = 'mj_' + num;
        this.node.setPosition(x, y);
        this.node.setScale(1.5, 1.5);
        this.node.on(NodeEventType.TOUCH_START, callback);
        this.node.getComponent(Sprite).spriteFrame = spriteFrame;
    }

    initialize(params) {
        // 在这里处理传入的参数
        console.log('Initialized with params:', params);
        // 例如：this.someProperty = params.someProperty;
    }

    update(deltaTime: number) {

    }

    getRandomInt(min, max) {
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }

}

